import Modal from "../modal/modal";
import img from "../../images/done.png";
import styles from './order-details.module.css';
import PropTypes from 'prop-types';
import { dataPropTypes } from "../utils/types";

export default function OrderDetails({data, handleClose}) {
    return (
        <Modal handleClose={handleClose} hasOverlay={true}>
            <p className={`${styles.number} text text_type_digits-large mt-30`}>034536</p>
            <p className="text text_type_main-medium mt-8"> </p>
            <img src={img} alt=" " className="mb-15 mt-15" />
            <p className="text text text_type_main-default mt-2">   </p>
            <p className="text text text_type_main-default text_color_inactive mb-30">    </p>
        </Modal>
    )
}

OrderDetails.propTypes = {
    data: dataPropTypes,
    handleClose: PropTypes.func.isRequired,
}